___CSS_LOADER_EXPORT___.push([module.id, `body,
html {
  margin: 0;
  min-height: 100vh;
}

h1 {
  color: #ffffff;
}

body {
  font-family: 'Mitr', sans-serif;

  background-color: #080404;

}

.conteudo {
  padding: 1%;

  gap: 10px;

  display: grid;
  place-items: center;

  color: #ffffff;
}

.grid-termo {
  display: grid;
  grid-template-rows: repeat(5, 65px);
  gap: 7px;

  margin-bottom: 2%;
}

.grid-termo>.linha {
  display: grid;
  grid-template-columns: repeat(5, 65px);
  gap: 7px;
}

.grid-termo>.linha>.letra {
  display: grid;
  place-items: center;
  height: 100%;

  background-color: #bebebe;
  color: #2e2e2e;
  border-radius: 3px;

  font-size: 22px;
  font-weight: 700;
}

.grid-teclado {
  display: grid;
  grid-template-columns: repeat(10, 35px);
  gap: 2.2px;
}

.grid-teclado>button {
  height: 35px;
  border: none;
  border-radius: 5px;
}

.grid-teclado>.btn-enter {
  grid-column: 2 span
}

.grid-teclado>button:hover {
  background-color: transparent;

  color: #32fe5e;

  font-weight: 800;
}

.btn-reiniciar {
  background-color: rgba(79, 99, 75, 0.342);
  color: #32fe5e;
  font-weight: 800;
  padding: 10px;
  margin: 5px;
}

.btn-reiniciar:hover {
  background-color: #27482e;
  border-radius: 5px;
}

.letra-correta{
  background-color: #0fda0fb6 !important
}

.letra-incorreta {
  background-color: #ffff00fa !important
}

.letra-inexistente {
  background-color: #b85e5e !important
}

.group-mensagem {
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal {
  font-family: 'Mitr', sans-serif;
}

.tentativas {
  background-color: lightgray;
  padding: 1px 6px;
  text-align: right;
}`, "",{"version":3,"sources":["webpack://./src/views/styles.css"],"names":[],"mappings":"AAAA;;EAEE,SAAS;EACT,iBAAiB;AACnB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,+BAA+B;;EAE/B,yBAAyB;;AAE3B;;AAEA;EACE,WAAW;;EAEX,SAAS;;EAET,aAAa;EACb,mBAAmB;;EAEnB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,mCAAmC;EACnC,QAAQ;;EAER,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sCAAsC;EACtC,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;;EAEZ,yBAAyB;EACzB,cAAc;EACd,kBAAkB;;EAElB,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,uCAAuC;EACvC,UAAU;AACZ;;AAEA;EACE,YAAY;EACZ,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE;AACF;;AAEA;EACE,6BAA6B;;EAE7B,cAAc;;EAEd,gBAAgB;AAClB;;AAEA;EACE,yCAAyC;EACzC,cAAc;EACd,gBAAgB;EAChB,aAAa;EACb,WAAW;AACb;;AAEA;EACE,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA;EACE;AACF;;AAEA;EACE;AACF;;AAEA;EACE;AACF;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,uBAAuB;AACzB;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,2BAA2B;EAC3B,gBAAgB;EAChB,iBAAiB;AACnB","sourcesContent":["body,\r\nhtml {\r\n  margin: 0;\r\n  min-height: 100vh;\r\n}\r\n\r\nh1 {\r\n  color: #ffffff;\r\n}\r\n\r\nbody {\r\n  font-family: 'Mitr', sans-serif;\r\n\r\n  background-color: #080404;\r\n\r\n}\r\n\r\n.conteudo {\r\n  padding: 1%;\r\n\r\n  gap: 10px;\r\n\r\n  display: grid;\r\n  place-items: center;\r\n\r\n  color: #ffffff;\r\n}\r\n\r\n.grid-termo {\r\n  display: grid;\r\n  grid-template-rows: repeat(5, 65px);\r\n  gap: 7px;\r\n\r\n  margin-bottom: 2%;\r\n}\r\n\r\n.grid-termo>.linha {\r\n  display: grid;\r\n  grid-template-columns: repeat(5, 65px);\r\n  gap: 7px;\r\n}\r\n\r\n.grid-termo>.linha>.letra {\r\n  display: grid;\r\n  place-items: center;\r\n  height: 100%;\r\n\r\n  background-color: #bebebe;\r\n  color: #2e2e2e;\r\n  border-radius: 3px;\r\n\r\n  font-size: 22px;\r\n  font-weight: 700;\r\n}\r\n\r\n.grid-teclado {\r\n  display: grid;\r\n  grid-template-columns: repeat(10, 35px);\r\n  gap: 2.2px;\r\n}\r\n\r\n.grid-teclado>button {\r\n  height: 35px;\r\n  border: none;\r\n  border-radius: 5px;\r\n}\r\n\r\n.grid-teclado>.btn-enter {\r\n  grid-column: 2 span\r\n