var LocalStorageService = /** @class */ (function () {
    function LocalStorageService() {
        this.key = 'jogoTermo:progresso';
    }
    LocalStorageService.prototype.salvarDados = function (progresso) {
        var progressoEmString = JSON.stringify(progresso);
        localStorage.setItem(this.key, progressoEmString);
    };
    LocalStorageService.prototype.obterDados = function () {
        var dados = localStorage.getItem(this.key);
        var progresso;
        if (dados) {
            progresso = JSON.parse(dados);
        }
        return progresso;
    };
    return LocalStorageService;
}());
export { LocalStorageService };
