import { acervo } from "../dominio/jogo/acervo-termo";
import { jogo, resultado } from "../dominio/jogo/jogo-termo";
import { Progresso } from "../dominio/progresso/progresso-termo";
import { LocalStorageService } from "../services/local-storage-service";
import './styles.css';
;
var telaTermo = /** @class */ (function () {
    function telaTermo(jogo, progresso) {
        this.jogo = jogo;
        this.progresso = progresso;
        this.letras = [];
        this.maxCol = 5;
        this.maxRow = 5;
        this.coluna = 0;
        this.linha = 0;
        this.palavra = "";
        this.atribuirEventos();
        this.pnlTermo = document.getElementById('pnlTermo');
        this.pnlConteudo = document.getElementById('pnlConteudo');
    }
    telaTermo.prototype.atribuirEventos = function () {
        var _this = this;
        this.btnEnter = document.getElementById('btnEnter');
        this.btnEnter.addEventListener('click', function () { return _this.verificarJogada(); });
        this.btnBack = document.getElementById('btnBackspace');
        this.btnBack.addEventListener('click', function () { return _this.corrigirInput(); });
        this.pnlTeclado = document.getElementById('pnlTeclado');
        for (var i = 0; i < this.pnlTeclado.children.length; i++) {
            var tecla = this.pnlTeclado.children.item(i);
            if (tecla.textContent != 'Enter' && tecla.id != 'btnBackspace')
                tecla.addEventListener('click', function (sender) { return _this.atribuirLetra(sender); });
        }
        this.btnHistorico = document.getElementById('btnHistorico');
        this.btnHistorico.addEventListener('click', function () { return _this.obterProgresso(); });
    };
    telaTermo.prototype.atribuirLetra = function (sender) {
        if (this.palavra.length === this.maxCol)
            return;
        var tbLetra = this.pnlTermo
            .children[this.linha]
            .children[this.coluna];
        var letra = sender.target;
        tbLetra.textContent = letra.innerText;
        this.palavra += letra.innerText;
        this.letras.push(tbLetra);
        this.coluna++;
    };
    telaTermo.prototype.verificarJogada = function () {
        if (this.palavra.length !== this.maxCol)
            return;
        this.colorirletras();
        if (this.jogo.acertou(this.palavra)) {
            this.enviarMensagem(true);
            this.salvarProgresso(true, this.linha);
        }
        else {
            this.linha++;
            if (this.linha === this.maxRow) {
                this.enviarMensagem(false);
                this.salvarProgresso(false);
            }
        }
        this.limparDados();
    };
    telaTermo.prototype.colorirletras = function () {
        var resultados = this.jogo.verificarJogada(this.palavra);
        for (var i = 0; i <= this.palavra.length; i++) {
            switch (resultados[i]) {
                case resultado.Acerto:
                    this.letras[i].classList.add('letra-correta');
                    break;
                case resultado.Erro:
                    this.letras[i].classList.add('letra-incorreta');
                    break;
                case resultado.Inexistente:
                    this.letras[i].classList.add('letra-inexistente');
                    break;
            }
        }
    };
    telaTermo.prototype.gerarElementosMsgFinal = function () {
        var _this = this;
        this.removerMsgFinal();
        var div = document.createElement('div');
        div.id = "divMensagemFinal";
        div.classList.add('group-mensagem');
        this.pnlConteudo.appendChild(div);
        var spanMsg = document.createElement('span');
        div.appendChild(spanMsg);
        var button = document.createElement('button');
        button.classList.add('btn-reiniciar');
        button.id = "btnReiniciar";
        button.innerText = "Recomeçar";
        button.addEventListener('click', function () { return _this.reiniciarJogo(); });
        div.appendChild(button);
        this.alterarStatusTeclado();
        return spanMsg;
    };
    telaTermo.prototype.enviarMensagem = function (venceu) {
        var msg = this.gerarElementosMsgFinal();
        if (venceu) {
            msg.textContent = "Parab\u00E9ns! Voc\u00EA acertou a palavra: ".concat(this.jogo.getPalavra());
        }
        else {
            msg.textContent = "Fim de Jogo! A palavra era: ".concat(this.jogo.getPalavra());
        }
    };
    telaTermo.prototype.corrigirInput = function () {
        if (this.coluna == 0)
            return;
        var letra = this.letras[this.coluna - 1];
        letra.textContent = "";
        this.letras.pop();
        this.coluna--;
        this.palavra = this.palavra.substring(0, this.palavra.length - 1);
    };
    telaTermo.prototype.reiniciarJogo = function () {
        this.limparDados();
        this.linha = 0;
        this.jogo = new jogo(new acervo());
        this.limparQuadros();
        this.removerMsgFinal();
        this.alterarStatusTeclado();
    };
    telaTermo.prototype.alterarStatusTeclado = function () {
        for (var index = 0; index < this.pnlTeclado.children.length; index++) {
            var tecla = this.pnlTeclado.children[index];
            tecla.disabled = !tecla.disabled;
        }
    };
    telaTermo.prototype.limparDados = function () {
        this.coluna = 0;
        this.palavra = "";
        this.letras = [];
    };
    telaTermo.prototype.removerMsgFinal = function () {
        var mensagemFinal = document.getElementById('divMensagemFinal');
        if (mensagemFinal) {
            this.pnlConteudo.removeChild(mensagemFinal);
        }
    };
    telaTermo.prototype.limparQuadros = function () {
        for (var i = 0; i < this.pnlTermo.children.length; i++) {
            var linha = this.pnlTermo.children.item(i);
            for (var index = 0; index < linha.children.length; index++) {
                var letra = linha.children.item(index);
                letra.classList.remove('letra-correta', 'letra-incorreta', 'letra-inexistente');
                letra.textContent = '';
            }
        }
    };
    telaTermo.prototype.salvarProgresso = function (acertou, linha) {
        this.progresso.atualizarJogada(acertou, linha);
    };
    telaTermo.prototype.obterProgresso = function () {
        var progresso = this.progresso.obterDados();
        if (!progresso)
            return;
        console.log(progresso);
        var valores = document.getElementById('valores');
        valores.children[0].textContent = progresso.jogos.toString();
        valores.children[1].textContent = progresso.porcentagemVitorias.toString() + "%";
        valores.children[2].textContent = progresso.sequenciaVitorias.toString();
        valores.children[3].textContent = progresso.melhorSequencia.toString();
        var historico = document.getElementById('historico');
        historico.children[1].lastChild.textContent = progresso.linhaDaJogada[0].toString();
        historico.children[2].lastChild.textContent = progresso.linhaDaJogada[1].toString();
        historico.children[3].lastChild.textContent = progresso.linhaDaJogada[2].toString();
        historico.children[4].lastChild.textContent = progresso.linhaDaJogada[3].toString();
        historico.children[5].lastChild.textContent = progresso.linhaDaJogada[4].toString();
        historico.children[6].lastChild.textContent = progresso.erros.toString();
    };
    return telaTermo;
}());
window.addEventListener("load", function () { return new telaTermo(new jogo(new acervo()), new Progresso(new LocalStorageService())); });
